<template>
  <div class="g_apps right">

    <!-- The Modal -->
    <div id="pinModal" class="modal">
      <!-- Modal content -->
    	<form @submit.prevent="enterPin()">
      <div class="modal-content">
        <span class="close" @click="closeModal('pinModal')">&times;</span>
        <h2>Pin</h2>

        <p style="color:#f11;font-size:small" v-if="errors.length">
          <b>Please correct the following error(s):</b>
          <ul class="errors">
            <li v-for="error in errors">{{ error }}</li>
          </ul>
        </p>

        <p><input type="password" name="pin" v-model="pin" placeholder="Pin" autofocus/><br />
        <input type="checkbox" value="true" name="rememberPin" v-model="rememberPin"> Remember pin</p>
        <p>
          <input value="Login" type="submit">
          <input value="Close" type="button" @click="closeModal('pinModal')">
        </p>
      </div>
      </form>
    </div>

    <nav>
    	<ul>
     		<li v-if="logged" class="dropdown">
          <a class="signinbox dropbtn">{{ getUsername() }}</a>
            <div class="dropdown-content">
              <a>Balance: {{ balance }} sat</a>
              <router-link to="/wallet/receive">Receive BCH</router-link>
              <router-link to="/wallet/send">Send BCH</router-link>
              <router-link to="/wallet/export">Export wallet key</router-link>
              <a href="/" @click="logout">Logout</a>
            </div>
        </li>
     		<li v-else><router-link to="/login" class="signinbox">Sign in</router-link></li>
    	</ul>
    </nav>
  </div>
</template>


<script>
  import userMixin from '@/mixins/userMixin'

  export default {
      name: 'UserHeader',
      mixins: [userMixin],
      props: {
      },
      data () {
        return {
          pin: null,
          rememberPin: null,
          errors: []
        }
      },
      mounted () {
      },
      computed: {
        logged: function () {
          if (this.loggedViewMode() || this.loggedSpendMode()) {
            return true
          }
          return false
        },
        balance: function () {
          if (this.addressInfo) {
            return this.addressInfo.balanceSat + this.addressInfo.unconfirmedBalanceSat
          }
          return 0
        }
      },
      methods: {
        enterPin () {
          this.errors = []
          if (this.rememberPin) {
            localStorage.setItem('user.pin', this.pin)
          } else {
            sessionStorage.setItem('user.pin', this.pin)
          }

          if (this.getAddress() != this.addressFromPrivateKey()) {
            this.errors.push('Incorrect pin')
            sessionStorage.removeItem('user.pin')
            localStorage.removeItem('user.pin')
            this.pin = ''
            return
          }

          this.$emit('pinEntered')
          this.closeModal('pinModal')
        }
      }
  };
</script>

<style>
nav ul {
    list-style-type: none;
    margin: 0;
    padding-top: 10px;
    padding-bottom: 10px;
   	top: 0;
}
nav li {
    float: right;
    text-align: center;
    margin-top: 10px;
}
nav li a {
    color: black;
    padding: 6px 14px;
    margin-top: 10px;
    text-decoration: none;
}
nav li a.signinbox {
    margin-right: 20px;
    border: 1px solid #4285f4;
    background: #4285f4;
    color: white;
    border-radius: 3px;
}
nav li a:hover:not(.signinbox) {
    text-decoration: underline;
}

/* Style The Dropdown Button */
.dropbtn {
}
.dropdown {
    position: relative;
    display: inline-block;
}
.dropdown-content {
    display: none;
    position: absolute;
    text-align: left;
    right: 0;
    margin-top: 6px;
    margin-right: 20px;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
.dropdown-content a:hover {background-color: #f1f1f1}
.dropdown:hover .dropdown-content {
    display: block;
}
.dropdown:hover .dropbtn {
}

</style>



