  width: 280px;
}
.form input[type="checkbox"]  {
  width: 15px;
}
.form .form-buttons {
  display: block;
  font-family: arial,sans-serif;
  padding-top: 18px;
  text-align: left;
}
.form .form-buttons input[type="submit"] {
    background-color: #f2f2f2;
    color: #757575;
    font-weight: bold;
    height: 36px;
    width: 142px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 4px;
    text-align: center;
}

.form .form-buttons input[type="submit"]:hover {
    border: 1px solid #c6c6c6;
}

.form .form-buttons input[type="submit"]:focus {
