  width: 15px;
}
.modal input[type="submit"] {
    color: #fff;
    background-color: #428bca;
    border-color: #357ebd;
    font-weight: bold;
    height: 36px;
    width: 100px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 10px 11px 0px;
