<template>
  <div class="grid">
    <div class="header">
      <div class="left">
        <div class="search">
          <a href="/">
            <img id="g_logo" src="../../assets/images/logo-small.png" srcset="../../assets/images/logo-small@2x.png 2x, ../../assets/images/logo-small@3x.png 3x" name="g_logo" />
          </a>
          <form @submit.prevent="changeSearchRoute()">
            <input type="text" value="searchTextInput" name="searchTextInput" v-model="searchTextInput" v-on:input="searchTextInput = $event.target.value" autocomplete="off" />
            <button class="loupe"><img  src="../../assets/images/search.png" srcset="../../assets/images/search@2x.png 2x, ../../assets/images/search@3x.png 3x" /></button>
          </form>
        </div>
      </div>
      <UserHeader/>
    </div>

    <div class="s_results" v-if="loading === false">
      <p>
        <button @click="changePeriod(7)" v-bind:class="{ active: (periodInput == 7) }">7 days</button>
        <button @click="changePeriod(30)" v-bind:class="{ active: (periodInput == 30) }">30 days</button>
        <button @click="changePeriod(90)" v-bind:class="{ active: (periodInput == 90) }">90 days</button>
        <button @click="changePeriod(365)" v-bind:class="{ active: (periodInput == 365) }">1 year</button>
      </p>
      <line-chart :data="chartData" :curve="false" :messages="{empty: 'No data'}" :dataset="{borderWidth: 3, pointRadius: 0}" :library="{tooltips: {mode: 'index', intersect: false}, hover: {mode: 'nearest', intersect: true}}"></line-chart>

    </div>
    <div class="s_results" v-else>
      <div class="n_results">
        Loading...
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import UserHeader from '@/components/UserHeader'
import Footer from '@/components/Footer'

export default {
  name: 'Result',
  props: {
    search: {type: String, required: true},
    period: {type: String, required: true}
  },
  data () {
    return {
      chartData: [],
      searchPhrases: [],
      searchTextInput: null,
      periodInput: null,
      periodFormat: null,
      loading: true,
      errored: false
    }
  },
  components: {
    UserHeader,
    Footer
  },
  mounted () {
    this.periodInput = parseInt(this.period)
    this.searchTextInput = Base64.decode(this.search)
    this.splitTextInput()
    this.populateChart()
  },
  methods: {
    dateFormater (a, t) {
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month_text = months[a.getMonth()]
      let month = ('0' + (a.getMonth() + 1)).substr(-2)
      let date = ('0' + (a.getDate())).substr(-2)
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      if (t === 1) {
        return year + '-' + month + '-' + date
      }
      return date + ' ' + month + ' ' + year + ' ' + hour + ':' + min
    },
    splitTextInput () {
      let tmpString = this.searchTextInput.replace(/['"]+/g, '')
      this.searchPhrases = tmpString.split(/\s*,\s*/)
    },
    changePeriod (period) {
      this.periodInput = parseInt(period)
      this.changeSearchRoute()
    },
    changeSearchRoute () {
      if (this.searchTextInput) {
        this.$router.push('/result/' + Base64.encode(this.searchTextInput) + '/' + this.periodInput)
        this.splitTextInput()
        this.populateChart()
      }
    },
    populateChart () {
      this.loading = true
      this.chartData = []

      for (let pcIndex = 0; pcIndex < this.searchPhrases.length; ++pcIndex) {
        this.getMatchCountArray(this.searchPhrases[pcIndex])
      }
    },
    getMatchCountArray (searchPhrase) {
      let nowTimestamp = new Date().getTime() / 1000
      let beginTimestamp = nowTimestamp - (this.periodInput * 24 * 60 * 60)
      let endTimestamp = nowTimestamp

      let periodFormat = '%Y-%m-%d'

      let matchPhrase = searchPhrase
      let queryMatch = {}
      queryMatch['$and'] = []
      if (matchPhrase.startsWith("prefix:")) {
        matchPhrase = matchPhrase.replace(/^prefix:\s*|\s*$/g, '')
        var prefixArray = matchPhrase.split(" ")
        queryMatch['$and'].push({'out.h1': prefixArray[0]})
        matchPhrase = matchPhrase.replace(/^\S+\s*/gu, '')
      }
      if (matchPhrase) {
        queryMatch['$and'].push({'$text': {'$search': '\"' + matchPhrase + '\"'}})
      }
      queryMatch['$and'].push({'blk.t': {'$gte': beginTimestamp, '$lte': endTimestamp}})

      let query = {
        'v': 3,
        'q': {
          'aggregate': [{
            '$match': queryMatch
          }, {
            '$group': {
              '_id': {
                '$dateToString': {
                  'format': periodFormat,
                  'date': {
                    '$toDate': {
                      '$multiply': [1000, '$blk.t']
                    }
                  }
                }
              },
              'count': {
                '$sum': 1
              }
            }
          }],
          'limit': 10000,
          'sort': {'_id': 1}
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = localStorage.getItem('bitdbNode') + b64

      // Attach API KEY as header
      let header = {
        headers: { key: localStorage.getItem('bitdbApiKey') }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data.c.length > 0) {
            let prevItemDate = null
            let singleChartData = []
            for (let index = 0; index < response.data.c.length; ++index) {
                let itemDate = new Date(response.data.c[index]['_id'])
                if (index === 0) {
                  // fill with 0 if it's not the beginning
                  let timeDiff = Math.abs(itemDate.getTime() - ((new Date()).getTime() - (this.periodInput * 24 * 60 * 60 * 1000)))
                  let diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24))
                  if (diffDays === 1) {
                    diffDays = 0
                  }

                  if (diffDays > 0) {
                    for (let j = diffDays; j > 0; --j) {
                      let fillDate = new Date(itemDate)
                      fillDate.setDate(itemDate.getDate() - j)
                      singleChartData.push([this.dateFormater(fillDate, 1), 0])
                    }
                  }
                } else {
                  let timeDiff = Math.abs(itemDate.getTime() - prevItemDate.getTime())
                  let diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24))
                  // fill empty dates with 0
                  if (diffDays > 1) {
                    for (let j = 1; j < diffDays; ++j) {
                      let fillDate = new Date(prevItemDate)
                      fillDate.setDate(prevItemDate.getDate() + j)
                      singleChartData.push([this.dateFormater(fillDate, 1), 0])
                    }
                  }
                }

                singleChartData.push([response.data.c[index]['_id'], response.data.c[index]['count']])
                prevItemDate = itemDate
            }

            // fill with 0 till the end
            let timeDiff = Math.abs((new Date()).getTime() - prevItemDate.getTime())
            let diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24))
            if (diffDays > 0) {
              for (let j = 1; j < diffDays; ++j) {
                let fillDate = new Date(prevItemDate)
                fillDate.setDate(prevItemDate.getDate() + j)
                singleChartData.push([this.dateFormater(fillDate, 1), 0])
              }
            }
            // push data to chart
            this.chartData.push({'name': searchPhrase, 'data': singleChartData})
          }
          this.loading = false
        })
        .catch(error => {
          console.log(error)
          this.errored = true
        })
    }
  }
}
</script>

<style>
.search input[type="text"]{
  box-sizing:border-box;
  height:46px;
  width: 630px;
  font-family: sans-serif;
  font-size: 16px;
  padding: 0 20px;
  border: 0.5px solid #d9d9d9;
  box-shadow: 0 4px 2px -2px #e6e6e6;
}
.search input[type="text"]:hover{
  box-shadow: 0px 2px 8px #dddddd;
  outline: none;
}
.loupe{
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  cursor:pointer;
  overflow: hidden;
  outline:none;
  position: absolute;
  top:10px;
  right:20px;
}
#all{
  color: #4285F4;
  font-weight: bold;
}
#profile_pic{
  width: 32px;
  height: 32px;
  border-radius: 100%;
}
.g_apps img{
  margin-left: 18px;
}
.s_results{
  grid-column: span 10;
  font-family: arial, sans-serif;
  margin: 15px 165px 51px;
}
h3{
  font-family: arial, sans-serif;
  font-size: 18px;
  font-weight: normal;
}
button.active {
  background-color: #fff;
  border: 0px;
}
.chart {
  width: 12fr
}

@media screen and (max-width: 768px) {
  .search input[type="text"]{
    box-sizing:border-box;
    height:46px;
    width: 350px;
    font-family: sans-serif;
    font-size: 16px;
    padding: 0 20px;
    border: 0.5px solid #d9d9d9;
    box-shadow: 0 4px 2px -2px #e6e6e6;
  }
}
</style>



