<template>
    <div class="grid">
      <HeaderWithTitle  v-bind:title="'Receive BCH'"/>

      <div class="s_results form">

        <p style="color:#191;font-size:small" v-if="receivedTxs">
          <ul class="notifications">
            <li v-for="item in receivedTxs.data">Transaction received: {{ item.tx.h }}</li>
          </ul>
        </p>

        <qriously :value="getAddress()" :size="300" />
        <p>{{ getAddress() }}</p>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderWithTitle from '@/components/HeaderWithTitle'
import Footer from '@/components/Footer'
import userMixin from '@/mixins/userMixin'

export default {
  name: 'WalletReceive',
  mixins: [userMixin],
  components: {
    Footer,
    HeaderWithTitle
  },
  data () {
    return {
    }
  },
  mounted () {
    if (!this.loggedViewMode() && !this.loggedSpendMode()) {
      this.$router.push('/login')
    }
  },
  methods: {
  }
}
</script>



