<template>
    <div class="grid">
      <HeaderWithTitle  v-bind:title="'Log In / Sign Up'"/>

      <div class="s_results form">

      <form class="ui form" @submit.prevent="login">
      <div v-if="isNewUser">
        <p style="color:#f11;font-size:small">Pin is used to encrypt your private key.</p>
        <p style="color:#f11;font-size:small">Trends.cash does not store any user data. We cannot recover your user data if you lose it.</p>
      </div>

        <div class="field-block">
          <h4>Private key</h4>
          <input type="text" name="privateKey" v-model="privateKey" placeholder="privateKey" required autocomplete="off">
        </div>

        <div class="field-block">
          <h4>Key format</h4>
          <select name="privateKeyFormat" v-model="privateKeyFormat">
            <option value="raw">Raw</option>
            <option value="wif">WIF</option>
          </select>
        </div>

        <div class="field-block">
          <h4>Pin code</h4>
          <input type="text" name="pin" v-model="pin" placeholder="Pin code" required autocomplete="off">
        </div>

        <div class="field-block">
          <input type="checkbox" value="true" name="rememberPrivateKey" v-model="rememberPrivateKey"> Remember private key
        </div>

        <div class="form-buttons">
          <input value="Sign up" type="submit" v-if="isNewUser">
          <input value="Log in" type="submit" v-else>
          <p style="color:#666;font-size:small">Are you <a href="javascript:void(0)" @click="newUser()">new user</a>?</p>
          <p style="color:#666;font-size:small">Log in with <router-link to="/login">Username and password</router-link></p>
        </div>
      </form>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderWithTitle from '@/components/HeaderWithTitle'
import Footer from '@/components/Footer'
import userMixin from '@/mixins/userMixin'

export default {
  name: 'LogInForm',
  mixins: [userMixin],
  components: {
    Footer,
    HeaderWithTitle
  },
  data () {
    return {
      isNewUser: false,
      privateKey: null,
      privateKeyFormat: 'raw',
      pin: null,
      rememberPrivateKey: null,
      notification: {
        message: '',
        type: ''
      }
    }
  },
  mounted () {
    if (this.loggedViewMode() || this.loggedSpendMode()) {
      this.$router.push('/')
    }
  },
  methods: {
    newUser () {
      this.isNewUser = true
    },
    login () {
      if (this.privateKey && this.privateKeyFormat && this.pin) {
        let pk = null
        if (this.privateKeyFormat === 'raw') {
          pk = bitcore.PrivateKey(this.privateKey)
        } else if (this.privateKeyFormat === 'wif') {
          pk = bitcore.PrivateKey.fromWIF(this.privateKey)
        }
        const address = pk.toAddress().toString()
        const encryptedPrivateKey = CryptoJS.AES.encrypt(pk.toString(), this.pin.toString())
        if (this.rememberPrivateKey) {
          localStorage.setItem('user.address', address)
          localStorage.setItem('user.username', address.substring(12, 20) + '...')
          localStorage.setItem('user.privateKey', encryptedPrivateKey)
        } else {
          sessionStorage.setItem('user.address', address)
          sessionStorage.setItem('user.username', address.substring(12, 20) + '...')
          sessionStorage.setItem('user.privateKey', encryptedPrivateKey)
        }
        sessionStorage.setItem('user.pin', this.pin)

        // redirect to user home
        this.$router.push('/')
      } else {
        this.privateKey = this.pin = ''
      }
    }
  }
}
</script>



