<template>
  <div class="grid">
    <HeaderWithTitle  v-bind:title="pageTitle"/>

    <div class="s_results page">
      <component :is="page" :key="$route.fullPath" />
    </div>
    <Footer/>
  </div>
</template>

<script>
import HeaderWithTitle from '@/components/HeaderWithTitle'
import Footer from '@/components/Footer'
import About from '@/components/Page/About'
import Disclaimer from '@/components/Page/Disclaimer'
import Settings from '@/components/Page/Settings'

export default {
  name: 'Page',
  props: {
    page: String
  },
  data () {
    return {
    }
  },
  components: {
    About,
    Disclaimer,
    Settings,
    Footer,
    HeaderWithTitle
  },
  mounted () {
  },
  computed: {
    pageTitle () {
      let resultString = this.page.replace(/([a-z])([A-Z])/g, '$1 $2')
      resultString = resultString.replace(/([A-Z])([A-Z][a-z])/g, '$1 $2')
      resultString = resultString.charAt(0).toUpperCase() + resultString.substr(1)
      return resultString
    }
  },
  methods: {
  }
}
</script>

<style>
.page {
  padding-bottom: 30px;
}
.page p {
  letter-spacing: .2px;
  line-height: 20px;
  margin: 4px 0 12px;
  color: rgba(0,0,0,0.87);
  font-family: Roboto,arial,sans-serif;
  font-size: 14px;
  font-weight: 400;
}
.page h2 {
  color: #3c4043;
  font-family: 'Google Sans',sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  margin: 0;
  padding: 32px 0 8px 0;
}
.page h3 {
  color: #3c4043;
  font-family: 'Google Sans',sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  margin: 24px 0 8px;
}
code {
  color: #060;
  font-family: monospace;
}
strong {
  font-weight: 500;
}
</style>



