// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import { Base64 } from 'js-base64'
import bitcore from 'bitcore-lib-cash'
import datacash from 'datacash'
import satoshiBitcoin from 'satoshi-bitcoin'
import CryptoJS from 'crypto-js'
import VueChartkick from 'vue-chartkick'
import VueQriously from 'vue-qriously'
import Chart from 'chart.js'
import router from './router'
import App from './App'

window.axios = axios
window.Base64 = Base64
window.CryptoJS = CryptoJS
window.bitcore = bitcore
window.datacash = datacash
window.satoshiBitcoin = satoshiBitcoin

if (!localStorage.getItem('bitdbNode')) {
  localStorage.setItem('bitdbNode', 'https://bitdb.network/q/')
}
if (!localStorage.getItem('bitdbApiKey')) {
  localStorage.setItem('bitdbApiKey', 'qpl84tsdqd6yxd3hpxhj5ngr3rp0pvlweqy7p7rzfy')
}
if (!localStorage.getItem('bitsocketNode')) {
  localStorage.setItem('bitsocketNode', 'https://bitsocket.org/s/')
}
if (!localStorage.getItem('blockExplorerRpc')) {
  localStorage.setItem('blockExplorerRpc', 'https://cashexplorer.bitcoin.com')
}

Vue.config.productionTip = false

Vue.use(VueQriously)
Vue.use(VueChartkick, {adapter: Chart})
VueChartkick.options = {
  library: {
    defaultFontColor: 'red'
  }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('toTitle', function (text) {
  let resultString = text.split(' ')[0]
  if (text.includes(' ')) {
    resultString = resultString + ' ' + text.substr(text.indexOf(' ') + 1).split(/[.|:|;|-|!|?|<|&]+/).slice(0, 1).join(' ')
  }
  return resultString
})



