<template>
    <div class="grid">
      <HeaderWithTitle  v-bind:title="'Send BCH'"/>

      <div class="s_results form">

        <p style="color:#f11;font-size:small" v-if="errors.length">
          <b>Please correct the following error(s):</b>
          <ul class="errors">
            <li v-for="error in errors">{{ error }}</li>
          </ul>
        </p>

        <p style="color:#191;font-size:small" v-if="notifications.length">
          <ul class="notifications">
            <li v-for="notification in notifications">{{ notification }}</li>
          </ul>
        </p>

              <form class="ui form" @submit.prevent="sendTransaction">

                <div class="field-block">
                  <h4>Address</h4>
                  <input type="text" name="receiverAddress" v-model="receiverAddress" placeholder="Address" required autocomplete="off">
                </div>

                <div class="field-block">
                  <h4>Amount (BCH)</h4>
                  <input type="text" name="amount" v-model="amount" placeholder="Amount (BCH)" required autocomplete="off">
                  <p style="color:#666;font-size:small" v-if="amount">({{ amountSat }} satoshis)</p>
                </div>

                <div class="form-buttons">
                  <input value="Send" type="submit" @click="submitted=true" :disabled="submitted">
                </div>
              </form>


      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderWithTitle from '@/components/HeaderWithTitle'
import Footer from '@/components/Footer'
import userMixin from '@/mixins/userMixin'

export default {
  name: 'WalletSend',
  mixins: [userMixin],
  components: {
    Footer,
    HeaderWithTitle
  },
  data () {
    return {
      receiverAddress: null,
      amount: null,
      submitted: false,
      notifications: [],
      errors: []
    }
  },
  mounted () {
    if (!this.loggedViewMode() && !this.loggedSpendMode()) {
      this.$router.push('/login')
    }
    if (!this.loggedSpendMode()) {
      this.openModal('pinModal')
    }
  },
  computed: {
    amountSat: function () {
      if (this.amount) {
        return satoshiBitcoin.toSatoshi(this.amount)
      }
      return
    }
  },
  methods: {
    sendTransaction () {
      this.errors = []

      if (this.addressInfo.balanceSat + this.addressInfo.unconfirmedBalanceSat <= this.amountSat) {
        this.submitted = false
        this.errors.push('Wallet has insufficient funds')
        return
      }
      if (this.amountSat < 546) {
        this.submitted = false
        this.errors.push('Amount is less than the minimum dust limit (546 sat).')
        return
      }

      let toArray = []
      let toElement = {}
      toElement['address'] = this.receiverAddress
      toElement['value'] = parseInt(this.amountSat)
      toArray.push(toElement)
      const pk = this.calculatePrivateKey()
      let that = this

      const tx = {
        'cash': {
          'key': pk,
          'to': toArray
        }
      }
      datacash.build(tx, function(error, res) {
        if (error) {
          console.log('datacash build failed: ', error)
          that.submitted = false
          return
        }

        if (that.addressInfo.balanceSat + that.addressInfo.unconfirmedBalanceSat <= that.amountSat + parseInt(res._estimateSize() * 1.4)) {
          that.submitted = false
          that.errors.push('Wallet has insufficient funds for fee (' + parseInt(res._estimateSize() * 1.4) + 'sat).')
          return
        }

        datacash.send({
          tx: res,
        }, function(error, hash) {
          that.receiverAddress = ''
          that.amount = ''
          that.submitted = false
          if (error) {
            that.errors.push('Transaction failed: ' + error)
            console.log('datacash failed: ', error)
            return
          }
          that.errors = []
          that.notifications.push('Transaction sent successfully. Transaction hash: ' + hash)
        })
      })
    }
  }
}
</script>



