<template>
  <div id="landingPage">
    <UserHeader/>

    <center>
      <img src="../../assets/images/logo-large.png" srcset="../../assets/images/logo-large@2x.png 2x, ../../assets/images/logo-large@3x.png 3x">
      <h1>Explore what Bitcoin Cash is saying</h1>
    	<form class="main" @submit.prevent="searchForText">
    		<div class="sbox">
    			<input type="text" name="searchText" v-model="searchText" v-on:input="searchText = $event.target.value" title="Explore Bitcoin Cash" placeholder="Enter a search term or a topic" autocomplete="off" autofocus>
    		</div>

    		<div class="buttons">
    			<input value="Explore" type="submit">
    		</div>
    	</form>

    	<div class="promo">
    		<a href="https://oyo.cash/" title="search Bitcoin Cash">Oyo.cash</a>
    	</div>
    </center>

    <Footer/>
  </div>
</template>

<script>
import UserHeader from '@/components/UserHeader'
import Footer from '@/components/Footer'

export default {
  name: 'SearchHome',
  data () {
    return {
      searchText: ''
    }
  },
  components: {
    UserHeader,
    Footer
  },
  methods: {
    searchForText () {
      if (this.searchText) {
        this.$router.push('/result/' + Base64.encode(this.searchText) + '/90')
      }
    }
  }
};
</script>

<style>
center {
    padding-top: 89px;
}

#landingPage h1 {
    color: #5f6368;
    font-family:"Google Sans Display", Heebo, Arial, sans-serif;
    font-size:40px;
    letter-spacing:normal;
    line-height:46px;
    font-weight: 400;
    padding: 13px 8px 8px 5px;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

form.main {
    width: 100%;
    text-align: center;
    margin-top: 50px;
    padding-bottom: 10px;
}

form.main input {
    width: 503px;
    height: 34px;
    text-align: left;
    font-size: 13px;
    border: none;
    outline: none;
}

form.main div.sbox{
    margin-bottom: 10px;
    height: 38px;
    width: 528px;
    overflow: hidden;
    padding: 6px 9px 0px 16px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
    border-radius: 2px;
    align-self: center;
    display: inline-block;
}
form.main div.sbox:hover {
   box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
}
form.main input[type="submit"] {
    background-color: #f2f2f2;
    color: #757575;
    font-weight: bold;
    height: 36px;
    width: 142px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 4px;
    text-align: center;
}
form.main input[type="submit"]:hover {
    border: 1px solid #c6c6c6;
}
form.main input[type="submit"]:focus {
    border: 1px solid #4d90fe;
}
</style>



