import Vue from 'vue'
import Router from 'vue-router'
import SearchHome from '@/components/Search/Home'
import SearchResult from '@/components/Search/Result'
import Page from '@/components/Page'
import LogInForm from '@/components/Auth/LogInForm'
import LogInPrivateKeyForm from '@/components/Auth/LogInPrivateKeyForm'
import WalletReceive from '@/components/Wallet/Receive'
import WalletSend from '@/components/Wallet/Send'
import WalletExport from '@/components/Wallet/Export'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: SearchHome
    },
    {
      path: '/result/:search/:period',
      component: SearchResult,
      props: true
    },
    {
      path: '/login',
      component: LogInForm
    },
    {
      path: '/login/privatekey',
      component: LogInPrivateKeyForm
    },
    {
      path: '/pages/:page',
      component: Page,
      props: true
    },
    {
      path: '/wallet/receive',
      component: WalletReceive
    },
    {
      path: '/wallet/send',
      component: WalletSend
    },
    {
      path: '/wallet/export',
      component: WalletExport
    }
  ]
})



